@import "./source/sass/variables.scss";
@import "./source/sass/mixin.scss";

.catalog-offer {
  background-color: $basic-gray-color;
  padding: 0 20px 20px 20px;
  display: grid;
  grid-template-rows: min-content min-content min-content;

  @media (max-width: $mobile-width-only) {
    grid-template-columns: auto 1fr min-content;
  }

  @media (min-width: $tablet-width) {
    padding: 0 0 63px 55px;
    margin: 0;
    grid-template-columns: min-content 1fr auto;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: auto 1fr min-content;
    box-sizing: border-box;
    margin: 0;
    flex-basis: 560px;
    max-width: 1070px;
    flex-grow: 1;
    min-height: 374px;
  }
}

.catalog-offer__title {
  padding: 40px 0 19px 0;
  margin: 0;

  @media (max-width: $mobile-width-only) {
    grid-column: 2 / 4;
    grid-row: 1 / 2;
  }

  @media (min-width: $tablet-width) {
    padding: 81px 0 0 0;
    margin: 0 0 26px 0;
    grid-column: 1 / 4;
    grid-row: 1 / 2;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 74px 0;
    grid-column: 2 / 4;
    grid-row: 1 / 2;
  }
}

.catalog-offer__text {
  padding: 0 0 24px 0;
  margin: 0 0 21px 0;
  border-bottom: 1px solid $bright-gray-color;

  @media (max-width: $mobile-width-only) {
    grid-column: 1 / 4;
    grid-row: 2 / 3;
  }

  @media (min-width: $tablet-width) {
    @include font-size(16px, 24px);
    border-bottom: none;
    margin: 0 0 33px 0;
    grid-column: 1 / 3;
    grid-row: 2 / 3;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1 / 4;
    grid-row: 2 / 3;
    margin: 0 0 13px 0;
  }
}

.catalog-offer__image {
  @media (max-width: $mobile-width-only) {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    align-self: end;
  }

  @media (min-width: $tablet-width) {
    grid-column: 3 / 4;
    grid-row: 1 / 4;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }

  &--slim {
    @media (max-width: $mobile-width-only) {
      margin: 0 20px 12px 0;
    }

    @media (min-width: $tablet-width) {
      width: 200px;
      height: 211px;
      margin: 90px 61px 0 20px;
    }

    @media (min-width: $desktop-width) {
      width: 100px;
      height: 106px;
      margin: 45px 60px 0 0;
    }
  }

  &--pro {
    @media (max-width: $mobile-width-only) {
      position: relative;
      right: 7px;
      margin: 0 4px 12px 0;
    }

    @media (min-width: $tablet-width) {
      width: 268px;
      height: 200px;
      margin: 97px 28px 0 17px;
    }

    @media (min-width: $desktop-width) {
      width: 134px;
      height: 100px;
      margin: 50px 30px 0 0;
      position: relative;
      right: 18px;
    }
  }
}

.catalog-offer__link {
  color: $black-color;
  text-transform: uppercase;
  position: relative;

  @include font-special(400);
  @include font-size(16px, 16px);

  @media (max-width: $mobile-width-only) {
    grid-column: 1 / 4;
    grid-row: 3 / 4;
  }

  @media (min-width: $tablet-width) {
    @include font-size(20px, 30px);
    grid-column: 1 / 4;
    grid-row: 3 / 4;
  }

  &::after {
    content: "";
    background-image: url("../img/icon-arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    top: 3px;
    width: 24px;
    height: 12px;
    margin: 0 0 0 18px;

    @media (min-width: $tablet-width) {
      top: 9px;
      margin: 0 0 0 26px;
    }

    @media (min-width: $desktop-width) {
      margin: 0 0 0 29px;
    }
  }

  &:hover {
    &::after {
      width: 32px;
    }
  }

  &:active {
    opacity: 0.3;
  }
}
